*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: calc(100vw / 140);

  @media (min-width: 1400px) {
    font-size: 10px;
  }

  @include small-tablet {
    font-size: calc(100vw / 76.8);
  }

  @include mobile {
    font-size: 10px;
  }
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title2 {
  margin: 0;
  font-weight: 400;
  font-size: 2rem;
  text-transform: uppercase;
  color: #303030;
}

.title3 {
  margin: 0;
  font-weight: 400;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: #303030;
}

.desc {
  margin: 0;
  font-weight: 400;
  font-size: 1.6rem;
  color: #303030;
  line-height: 140%;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 120%;
  text-transform: uppercase;
  color: #000;
  border: 1px solid #b4b4b4;
  background-color: #fff;
  transition: .4s ease-out;

  @include hover {
    background-color: #000;
    color: #fff;
  }
}


.about-collection__wrapper {
  display: flex;
  flex-direction: column;
}
